.bottomAppbar {
  /* width: 100vw; */
  background-color: white;
  height: 70px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 20;
}
.tabs {
  display: flex;
  flex-direction: row;
  height: 100%;
}
.tab {
  background-color: #fff;
  width: 33.4%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-top: 1px solid #eee;
  box-shadow: 1x 1x 3px #ccc, -1px -1px 3px #ccc;
  font-size: 14px;
}
.tabLeft {
  width: 100%;
  border-top-right-radius: 30px;
  border-top: 1px solid rgba(167, 161, 161, 0.69);
  box-shadow: 0px 6px 7px 0px rgba(23, 23, 23, 0.28);
}
.tabRight {
  width: 100%;
  border-top-left-radius: 30px;
  border-top: 1px solid rgba(167, 161, 161, 0.69);
  box-shadow: 15px 4px 15px 0px rgba(23, 23, 23, 0.28);
}
.tabFab {
  width: 180px;
  height: 100%;
  background: transparent;
  border: none;
  display: flex;
}
.top {
  width: 100%;
  height: 50%;
  border-bottom-left-radius: 100px;
  border-bottom-right-radius: 100px;
  background-color: transparent;
  box-shadow: 0px 25px 0px 10px rgb(255, 255, 255);
  border-bottom: 1px solid rgba(167, 161, 161, 0.69);
  display: flex;
  justify-content: center;
  /* z-index: 0; */
}
span {
  font-size: 10px;
}

.fab {
  border-radius: 50%;
  background-color: #f4d03f;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  font-weight: bold;
  font-size: 22px;
  color: #fff;
  position: relative;
  /* transform: translate(2px,-60%); */
  transform: translateY(-60%);
}

.additem {
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: none;
  color: rgb(255, 255, 255);
}

.menuNav {
  text-align: center;
}
.iconsBar {
  text-align: center;
  font-size: 25px;
  /* width: 1.6rem; */
}
.barLink {
  text-decoration: none;
  color: rgb(152, 152, 152);
}
